"use client"

import type React from "react"
import { useState, useRef, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Send, Music, User, Search, Loader2 } from "lucide-react"
import { sendMessage, getConversation, markMessagesAsRead, getStudentsWithMessages } from "@/app/messages/actions"
import type { Message, Profile } from "@/lib/supabase/database.types"

type StudentWithMessages = Profile & {
  lastMessage: Message | null
  unreadCount: number
}

export function AdminChat() {
  const [students, setStudents] = useState<StudentWithMessages[]>([])
  const [selectedStudent, setSelectedStudent] = useState<StudentWithMessages | null>(null)
  const [messages, setMessages] = useState<Message[]>([])
  const [newMessage, setNewMessage] = useState("")
  const [searchQuery, setSearchQuery] = useState("")
  const [isLoadingStudents, setIsLoadingStudents] = useState(true)
  const [isLoadingMessages, setIsLoadingMessages] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  // Load students on mount
  useEffect(() => {
    async function loadStudents() {
      setIsLoadingStudents(true)
      const { students: studentData } = await getStudentsWithMessages()
      setStudents(studentData)
      setIsLoadingStudents(false)
    }

    loadStudents()
  }, [])

  // Load messages when student is selected
  useEffect(() => {
    if (!selectedStudent) return

    const studentId = selectedStudent.id

    async function loadMessages() {
      setIsLoadingMessages(true)
      const { messages: conversationMessages } = await getConversation(studentId)
      setMessages(conversationMessages)

      // Mark messages as read
      await markMessagesAsRead(studentId)

      // Update unread count in sidebar
      setStudents(prev => prev.map(s =>
        s.id === studentId ? { ...s, unreadCount: 0 } : s
      ))

      setIsLoadingMessages(false)
    }

    loadMessages()
  }, [selectedStudent])

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  // Poll for new messages when a student is selected
  useEffect(() => {
    if (!selectedStudent) return

    const interval = setInterval(async () => {
      const { messages: newMessages } = await getConversation(selectedStudent.id)
      setMessages(newMessages)
    }, 5000)

    return () => clearInterval(interval)
  }, [selectedStudent])

  const handleSelectStudent = (student: StudentWithMessages) => {
    setSelectedStudent(student)
  }

  const handleSendMessage = async () => {
    if (!newMessage.trim() || !selectedStudent) return

    setIsSending(true)
    const result = await sendMessage(selectedStudent.id, newMessage.trim())
    setIsSending(false)

    if (result.success && result.message) {
      setMessages((prev) => [...prev, result.message])
      setNewMessage("")

      // Update last message in sidebar
      setStudents(prev => prev.map(s =>
        s.id === selectedStudent.id
          ? { ...s, lastMessage: result.message }
          : s
      ))
    } else if (result.error) {
      alert(`Failed to send message: ${result.error}`)
    }
  }

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }

  const formatTimestamp = (timestamp: string) => {
    const date = new Date(timestamp)
    const now = new Date()
    const diffDays = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60 * 24))

    if (diffDays === 0) {
      return date.toLocaleTimeString("en-US", { hour: "numeric", minute: "2-digit" })
    } else if (diffDays === 1) {
      return "Yesterday"
    } else if (diffDays < 7) {
      return date.toLocaleDateString("en-US", { weekday: "short" })
    } else {
      return date.toLocaleDateString("en-US", { month: "short", day: "numeric" })
    }
  }

  const filteredStudents = students.filter((student) =>
    (student.name || '').toLowerCase().includes(searchQuery.toLowerCase()),
  )

  const totalUnread = students.reduce((acc, s) => acc + s.unreadCount, 0)

  // Check if message is from admin (current user) or student
  const isFromAdmin = (message: Message) => {
    return selectedStudent ? message.sender_id !== selectedStudent.id : false
  }

  return (
    <Card className="h-[600px] overflow-hidden">
      <div className="grid grid-cols-[320px_1fr] h-full">
        {/* Student Sidebar - Always Visible */}
        <div className="border-r flex flex-col bg-muted/30 h-full">
          <div className="p-4 border-b">
            <div className="flex items-center justify-between mb-3">
              <h3 className="font-serif font-semibold text-lg">Students</h3>
              {totalUnread > 0 && <Badge variant="destructive">{totalUnread} new</Badge>}
            </div>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search students..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
          </div>

          <ScrollArea className="flex-1">
            <div className="p-2">
              {isLoadingStudents ? (
                <div className="flex justify-center py-8">
                  <Loader2 className="h-6 w-6 animate-spin text-muted-foreground" />
                </div>
              ) : filteredStudents.length === 0 ? (
                <p className="text-center text-muted-foreground py-8 text-sm">No students found</p>
              ) : (
                filteredStudents.map((student) => (
                  <button
                    key={student.id}
                    onClick={() => handleSelectStudent(student)}
                    className={`w-full p-3 rounded-lg text-left transition-colors mb-1 ${selectedStudent?.id === student.id ? "bg-primary text-primary-foreground" : "hover:bg-muted"
                      }`}
                  >
                    <div className="flex items-start gap-3">
                      <div
                        className={`h-10 w-10 rounded-full flex items-center justify-center shrink-0 ${selectedStudent?.id === student.id ? "bg-primary-foreground/20" : "bg-primary/10"
                          }`}
                      >
                        <User
                          className={`h-5 w-5 ${selectedStudent?.id === student.id ? "text-primary-foreground" : "text-primary"
                            }`}
                        />
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center justify-between gap-2">
                          <span className="font-medium truncate">{student.name || 'Unknown'}</span>
                          {student.unreadCount > 0 && selectedStudent?.id !== student.id && (
                            <Badge
                              variant="destructive"
                              className="h-5 w-5 p-0 flex items-center justify-center text-xs shrink-0"
                            >
                              {student.unreadCount}
                            </Badge>
                          )}
                        </div>
                        {student.lastMessage && (
                          <div className="flex items-center justify-between gap-2 mt-0.5">
                            <p
                              className={`text-xs truncate ${selectedStudent?.id === student.id
                                ? "text-primary-foreground/70"
                                : "text-muted-foreground"
                                }`}
                            >
                              {student.lastMessage.sender_id !== student.id ? "You: " : ""}
                              {student.lastMessage.content}
                            </p>
                            <span
                              className={`text-[10px] shrink-0 ${selectedStudent?.id === student.id
                                ? "text-primary-foreground/60"
                                : "text-muted-foreground"
                                }`}
                            >
                              {formatTimestamp(student.lastMessage.created_at)}
                            </span>
                          </div>
                        )}
                      </div>
                    </div>
                  </button>
                ))
              )}
            </div>
          </ScrollArea>
        </div>

        {/* Main Chat Window */}
        <div className="flex flex-col h-full overflow-hidden">
          {selectedStudent ? (
            <>
              {/* Chat Header */}
              <div className="p-4 border-b flex items-center gap-3 shrink-0">
                <div className="h-10 w-10 bg-primary/10 rounded-full flex items-center justify-center">
                  <User className="h-5 w-5 text-primary" />
                </div>
                <div className="flex-1">
                  <h3 className="font-serif font-semibold">{selectedStudent.name || 'Student'}</h3>
                  <p className="text-xs text-muted-foreground">
                    {selectedStudent.email}  {selectedStudent.credits} credits
                  </p>
                </div>
                {Number(selectedStudent.balance_due) > 0 && (
                  <Badge variant="destructive">${Number(selectedStudent.balance_due).toFixed(2)} due</Badge>
                )}
              </div>

              {/* Messages */}
              <ScrollArea className="flex-1 p-4">
                <div className="space-y-3">
                  {isLoadingMessages ? (
                    <div className="flex flex-col items-center justify-center py-12">
                      <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
                      <p className="text-sm text-muted-foreground mt-2">Loading messages...</p>
                    </div>
                  ) : messages.length === 0 ? (
                    <div className="flex flex-col items-center justify-center h-full text-center py-12">
                      <Music className="h-10 w-10 text-muted-foreground mb-3" />
                      <p className="text-muted-foreground">No messages yet</p>
                      <p className="text-sm text-muted-foreground">Start a conversation with {selectedStudent.name}</p>
                    </div>
                  ) : (
                    messages.map((message) => (
                      <div
                        key={message.id}
                        className={`flex ${isFromAdmin(message) ? "justify-end" : "justify-start"}`}
                      >
                        <div
                          className={`max-w-[70%] rounded-2xl px-4 py-2.5 ${isFromAdmin(message)
                            ? "bg-primary text-primary-foreground rounded-br-sm"
                            : "bg-muted rounded-bl-sm"
                            }`}
                        >
                          <p className="text-sm leading-relaxed">{message.content}</p>
                          <p
                            className={`text-[10px] mt-1 ${isFromAdmin(message) ? "text-primary-foreground/60" : "text-muted-foreground"
                              }`}
                          >
                            {formatTimestamp(message.created_at)}
                          </p>
                        </div>
                      </div>
                    ))
                  )}
                  <div ref={messagesEndRef} />
                </div>
              </ScrollArea>

              {/* Input */}
              <div className="border-t p-4 shrink-0">
                <div className="flex gap-2">
                  <Input
                    placeholder={`Message ${selectedStudent.name}...`}
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyDown={handleKeyDown}
                    className="flex-1"
                    disabled={isSending}
                  />
                  <Button onClick={handleSendMessage} disabled={!newMessage.trim() || isSending}>
                    {isSending ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : <Send className="h-4 w-4 mr-2" />}
                    Send
                  </Button>
                </div>
              </div>
            </>
          ) : (
            <div className="flex-1 flex flex-col items-center justify-center text-center p-8 bg-muted/10">
              <div className="h-20 w-20 rounded-full bg-primary/10 flex items-center justify-center mb-6">
                <Music className="h-10 w-10 text-primary" />
              </div>
              <h3 className="font-serif text-2xl font-semibold mb-2">Select a Conversation</h3>
              <p className="text-muted-foreground max-w-sm">
                Choose a student from the list on the left to view their message history and send replies
              </p>
            </div>
          )}
        </div>
      </div>
    </Card>
  )
}
